.Introduce {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;

  .head {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-right: 20px;
    width: 100%;

    .selfie {
      margin-top: 20px;
      width: 350px;
      height: 350px;
      border-radius: 50%;
      border: 5px solid black;
      transition: 0.5s;
      cursor: pointer;

      &:hover {
        width: 380px;
        height: 380px;
        transition: 0.5s;
      }
    }

    .typingtext {
      font-weight: bold;
      font-family: "Nanum Gothic", sans-serif;
      font-size: 20px;
      color: grey;
      margin-top: 80px;
    }
  }

  .resumeImg {
    margin-top: 100px;
  }
}
